import React from 'react'
import { DashboardLayout } from '../../components/layout/DashboardLayout'
import { Construction, AlertTriangle } from 'lucide-react'

export default function DashboardPage() {
  return (
    <DashboardLayout>
      <div className="flex items-center justify-center min-h-[60vh]">
        <div className="text-center space-y-6 max-w-md">
          <div className="w-24 h-24 bg-orange-100 rounded-full flex items-center justify-center mx-auto">
            <Construction className="w-12 h-12 text-orange-600" />
          </div>
          
          <div className="space-y-2">
            <h1 className="text-2xl font-bold text-neutral-900">
              Página em Construção
            </h1>
            <p className="text-neutral-600">
              Esta página está sendo desenvolvida e estará disponível em breve.
            </p>
          </div>

          <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4 flex items-start space-x-3">
            <AlertTriangle className="w-5 h-5 text-yellow-600 flex-shrink-0 mt-0.5" />
            <div className="text-left">
              <p className="text-sm font-medium text-yellow-800">
                Dashboard temporariamente indisponível
              </p>
              <p className="text-sm text-yellow-700 mt-1">
                Enquanto isso, você pode acessar outras funcionalidades do sistema através do menu lateral.
              </p>
            </div>
          </div>

          <div className="text-sm text-neutral-500">
            <p>Volte em breve para conferir as novidades!</p>
          </div>
        </div>
      </div>
    </DashboardLayout>
  )
}